* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Arial, Helvetica, sans-serif;
}

ol,
ul {
  list-style: none;
}

main {
  max-width: 960px;
  margin: 64px auto;
}

header {
  text-align: center;
}

header h1 {
  font-size: 34px;
  margin-bottom: 8px;
}

header h2 {
  font-size: 16px;
  margin-bottom: 32px;
}

header address {
  border-top: 1px solid;
  border-bottom: 1px solid;
  padding: 16px;
  font-style: normal;
}

header address>ul {
  display: flex;
  justify-content: center;
}

header address>ul li {
  padding-right: 8px;
}

header address>ul li:not(:last-child)::after {
  padding-left: 8px;
  content: "•";
}

section {
  border-bottom: 1px solid;
  padding: 32px 16px 32px 16px;
}

section>ol>li {
  display: flex;
  margin-bottom: 16px;
}

section>ol>li div:nth-child(1) {
  width: 25%;
}

section>ol>li div:nth-child(2) {
  width: 75%;
}

section>ol>li ul>li {
  margin-bottom: 8px;
  list-style: circle;
}

h3 {
  text-align: center;
  text-transform: capitalize;
  margin-bottom: 16px;
  font-family: 'Times New Roman', Times, serif;
  font-style: italic;
}

section h4,
section h5 {
  margin-bottom: 8px;
}

section>ul {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

section>ul li {
  padding: 0 8px 8px 0;
}

section>ul li:not(:last-child)::after {
  padding-left: 8px;
  content: "•";
}


.skills {
  border-bottom: 1px solid;
  padding: 32px 16px 32px 16px;
}

.skills li {
  display: flex;
}

.skills li div {
  width: 50%;
  padding: 4px;
}

.skills li div:nth-child(1) {
  text-align: right;
  font-family: 'Times New Roman', Times, serif;
}


.social {
  padding: 32px 16px 32px 16px;
  text-align: center;
}